import { fetchPokemonPending, fetchPokemonSuccess } from './action'

//const nbPokemonsACharger = 151

//'https://pokeapi.co'


// tableau de fetch

export default (nbPokemonsACharger) => {
  console.log(nbPokemonsACharger)
  const urls = [] 
  for (let i = 1; i <= nbPokemonsACharger; i++ ) {
    urls.push(`https://pokeapi.co/api/v2/pokemon-species/${i}`)
  }
  const requests = urls.map(url => fetch(url))
  return dispatch => {
    dispatch(fetchPokemonPending())
    Promise.all(requests)
      .then(reponses => Promise.all(reponses.map(res => res.json())))
      .then(pokemons => pokemons.map(pokemon => ({
          id: pokemon.id,
          name: pokemon.names[4].name,
          captureRate: pokemon.capture_rate,
          isCatch: false,
          image: `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${pokemon.id}.png`
        }))).then(pokemons => dispatch(fetchPokemonSuccess(pokemons)))
  }
}