import React from "react";
import { connect } from 'react-redux'

import PokemonItem from "./PokemonItem";

const PokeList = ({nbClics, pokemons, pending }) => {

  //console.log(pending)
  //console.log(pokemons)

  const listePokemons = pokemons.map(pokemon => (
      <PokemonItem 
        key={pokemon.id}
        pokemon={pokemon}
        />
    )
  )
  

  return (
    <div className="list-container">
      <h2>Pokéballs lancées:<br />{nbClics}</h2>
      <h2>
        Pokedex: <br/>
        {pokemons.filter(pokemon => pokemon.isCatch).length} / {pokemons.length}
      </h2>
      <ul className='liste-pokemons'>
        {listePokemons}
      </ul>
    </div>
  );
};

const mapStateToProps = ({ nbClics, pokemons, pending }) => {
  return {
    nbClics,
    pokemons,
    pending
  }
}

export default connect(mapStateToProps)(PokeList);
