export const CLICK = 'CLICK'
export const FETCH_POKEMON_SUCCESS = 'FETCH_POKEMON_SUCCESS'
export const FETCH_POKEMON_PENDING = 'FETCH_POKEMON_PENDING'
export const SHOW_POKEMON = 'SHOW_POKEMON'
export const CATCH_POKEMON = 'CATCH_POKEMON'

export const fetchPokemonSuccess = (pokemons) => ({
  type: FETCH_POKEMON_SUCCESS,
  pokemons
})

export const fetchPokemonPending = () => ({
  type: FETCH_POKEMON_PENDING
})

export const showPokemonAction = (pokemons) => {
  const uncaughtPokemons = pokemons.filter(pokemon => !pokemon.isCatch)

  //if (uncaughtPokemons.length > 0)
  const random = Math.floor(Math.random()*uncaughtPokemons.length)
  const onScreen = uncaughtPokemons[random]
  console.log(random)
  console.log(onScreen)
  return dispatch => {
    dispatch({ type: SHOW_POKEMON, onScreen })
  }
}

export const catchPokemonAction = (pokemon) => {
  if (pokemon) {
    // 255 car taux de capture c'est un nombre entre 0 et 255
    const random = Math.floor(Math.random()*255)
    console.log('tentative de capture : ' + (random+pokemon.captureRate))
    
    return dispatch => {
      dispatch({ type: CATCH_POKEMON, random })
      dispatch({ type: CLICK })
    }
  }
  return () => {console.log('capture mais avec aucun pokemon affiché')}
}