import React, { useEffect } from "react";
import "./styles.css";
import { connect } from 'react-redux'

import GameBoy from "./components/GameBoy";
import PokeList from "./components/PokeList";
import fetchPokemons from './store/fetchPokemons';
import Loader from './components/Loader'

import { showPokemonAction, catchPokemonAction } from './store/action'

const App = ({ 
    fetchPokemons, 
    pending, 
    nbPokemonsACharger, 
    handleClickA,
    handleClickB,
    handleClickStart,
    handleClickSelect,
    pokemons,
    onScreen}) => {
  useEffect( () => {
    fetchPokemons(nbPokemonsACharger)
    }, [fetchPokemons, nbPokemonsACharger]
  )

  if (pending) {
    return <Loader />
  }

  return (
    <div className="App">
      <GameBoy
        handleClickA={() => handleClickA(onScreen)}
        handleClickB={() => handleClickB(pokemons)}
        handleClickStart={() => handleClickStart(pokemons)}
        handleClickSelect={() => handleClickSelect(pokemons)}
        />
      <PokeList />
    </div>
  );
};

const mapStateToProps = ({ nbPokemonsACharger, pending, pokemons, onScreen }) => {
  return {
    nbPokemonsACharger,
    pending,
    pokemons,
    onScreen
  }
}

const mapDispatchToProps = dispatch => {
  const showPokemon = pokemons => dispatch(showPokemonAction(pokemons))
  return {
    handleClickA: pokemon => dispatch(catchPokemonAction(pokemon)),
    handleClickB: showPokemon,
    handleClickStart: showPokemon,
    handleClickSelect: showPokemon,
    fetchPokemons: nbPokemons => dispatch(fetchPokemons(nbPokemons))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
