import initialState from './initialState'

import { CATCH_POKEMON, CLICK, FETCH_POKEMON_PENDING, FETCH_POKEMON_SUCCESS, SHOW_POKEMON } from './action'

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case CLICK:
      return {
        ...state, 
        nbClics: state.nbClics+1
      }
    case FETCH_POKEMON_SUCCESS:
      return {
        ...state, 
        pokemons: action.pokemons,
        pending: false
      }
    case FETCH_POKEMON_PENDING:
      return {
        ...state, 
        pending: true
      }
    case SHOW_POKEMON:
      return {
        ...state, 
        onScreen: action.onScreen
      }
    case CATCH_POKEMON:
      return {
        ...state, 
        pokemons: state.pokemons.map(pokemon=> {
          if (pokemon.id === state.onScreen.id) {
            const isCaught = (action.random + pokemon.captureRate) >= 255
            return {
              ...pokemon, 
              isCatch: (pokemon.isCatch || isCaught)
            }
          }
          return pokemon
        })
      }
    default:
      return state
  }
}

export default reducer