import React from "react";
import { connect } from 'react-redux'

const Screen = ( { onScreen, pokemons, nbClics } ) => {
  const gameBoyName = 'GAME BOY'

  if (onScreen && onScreen.id) {
    if (pokemons[onScreen.id-1].isCatch) {
      return (
        <div>
          <img src='https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/items/poke-ball.png' alt='poke-ball' />
          <h3 className='screen-name'>Capturé !</h3>
        </div>
      )
    }

    return (
      <div>
        <img src={onScreen.image} alt={onScreen.name} />
        <h3 className='screen-name'>{onScreen.name}{onScreen.isCatch ? ' Capturé': ''}</h3>
      </div>
    )
  }

  const nbPokemonsCaptures = pokemons.filter(pokemon => pokemon.isCatch).length

  if (nbPokemonsCaptures === pokemons.length) {
    return (
      <div>
        <h3>Partie terminée !</h3>
        <p className='screen-ending-text'>Pokédex rempli à 100%<br/>Il vous a fallu {nbClics} tentatives</p>
      </div>
    )
  }

  return (
    <>
      <div className="screen-logo">{gameBoyName}</div>
      <div className="screen-logo-shadow">{gameBoyName}</div>
      <div className="nintendo-logo-screen">Nintendo&reg;</div>
    </>
  );
};

const mapStateToProps = ({onScreen, pokemons, nbClics}) => {
  return {
    onScreen,
    pokemons,
    nbClics
  }
}

export default connect(mapStateToProps)(Screen);
